import axios from 'axios'
const baseURL = '/api/persons'

const getAll = () => {
    const request = axios.get(baseURL)
    return request.then(response => response.data)
}


const create = async newPerson => {
    const persons = await getAll()
    console.log(persons)
    const isDuplicate = persons.some(person => {
        return (person.name === newPerson.name)
    })
    if (isDuplicate) {
        const confirmation = window.confirm(`${newPerson.name} is already added to the phonebook, replace the old number with a new one?`)
        const person = persons.find(p => p.name === newPerson.name)
        if (confirmation) {
            const index = persons.findIndex(person)
            persons[index] = newPerson
            update(person.id, newPerson)
        }
        return persons

    } else {
        const request = axios.post(baseURL, newPerson)
        persons.push(newPerson)
        return persons
            //return request.then(response => response.data)
    }

}

const remove = id => {
    const request = axios.delete(`${baseURL}/${id}`)
    return request.then(response => response.data)
}

const update = (id, newPerson) => {
    const request = axios.put(`${baseURL}/${id}`, newPerson)
    return request.then(response => response.data)
}

export default { getAll, create, update, remove }